.SkillsIconContainer {
  width: 65%;
  padding: 15%;
  height: fit-content;
  border-radius: 15px;

  fill: #5bc0be;
  box-sizing: border-box;

  font-size: 2.5vh;
  text-align: center;
  margin: auto;
}

.BackdropLogo {
  position: absolute;
  width: 50vw;
  top: 0;
  left: 0;
  z-index: -1;
  margin-left: 30vw;
  margin-right: 20vw;
  margin-top: 16vh;
  opacity: 0.1;
  animation: modalgrow 1s forwards;

}

@keyframes SkillIconSpin {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

@keyframes modalgrow {
  0% {
    transform: scaleY(0);
  }

  100% {
    transform: scaleY(1);
  }
}

@keyframes modaltitleslide {
  0% {
    transform: translateX(40vw);
  }

  100% {
    transform: translateX(0vw);
  }
}

.popup {
  position: fixed;

  border-radius: 10px;
  display: flex;
  flex-direction: column;
  z-index: 100;
  width: 25vw;
  background-color: rgba(11, 19, 43, 0.7);
  height: fit-content;
  min-height: 30vh;
  color: white;

  justify-content: flex-start;
  padding: 1%;
  animation: modalgrow 0.2s forwards;
  overflow: hidden;
}

.popup > h3 {
  align-self: flex-start;
  margin: 0;
  animation: modaltitleslide 0.5s forwards;
}

.popup > p {
  margin-top: 0;
  width: 100%;
  border-top: thin solid #5bc0be;
  text-align: left;
}

@media (max-width: 1050px) {
  .popup {
    display: none;
  }

  .BackdropLogo {
    display: none;
  }
}
